/* App.css */

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #f4f4f4;
  min-height: 100vh;
}

.drop-zone {
  width: 50%;
  padding: 20px;
  border: 2px dashed #ccc;
  background-color: white;
  text-align: center;
  border-radius: 10px;
  margin-bottom: 20px;
}

.file-input {
  margin-top: 10px;
}

.file-list {
  width: 50%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.file-item {
  padding: 10px;
  border: 1px solid #ddd;
  background-color: #eaeaea;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  cursor: grab;
}

.btn {
  padding: 10px 20px;
  margin: 10px;
  border: none;
  background-color: #007bff;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}

.btn:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.btn:hover:not(:disabled) {
  background-color: #0056b3;
}
